//组件
import DataTable from './components/DataTable.vue';
import PatternSelect from './components/PatternSelect.vue';
import fontSelect from './components/fontSelect.vue'; // 其他

import { createImageAndDownload } from '@/utils/index';
import * as XLSX from 'xlsx';
import bus from './utils/store';
import { patternEnums, fontEnums } from "./utils/enums";
import JSZip from "jszip";
import saveAs from "file-saver";

const files = require.context('./components/Templates', false, /\.vue$/);

const templates = {};
files.keys().forEach(key => {
  templates[key.match(/([^/]*)\.vue$/)[1]] = files(key).default;
});
export default {
  components: { ...templates,
    DataTable,
    PatternSelect,
    fontSelect
  },

  data() {
    this.patternEnums = patternEnums;
    this.fontEnums = fontEnums;
    this.bus = bus;
    return {
      pattern: '库洛米',
      font: '幼圆',
      building: false,
      curIndex: 0,
      dpi: 1
    };
  },

  computed: {
    formatterData() {
      if (this.building) {
        return [this.bus.dataArr[this.curIndex]];
      } else {
        return this.bus.dataArr;
      }
    }

  },
  watch: {
    dpi: {
      handler() {
        document.documentElement.style.fontSize = `${this.dpi}px`;
      },

      immediate: true
    }
  },
  methods: {
    generateFile() {
      let promiseLine = Promise.resolve();
      const zip = new JSZip();
      const img = zip.folder("images"); //文件夹的名字

      this.building = true;
      this.curIndex = 0;
      let len = this.bus.dataArr.length;

      for (let i = 0; i < len; i++) {
        promiseLine = promiseLine.then(() => {
          return new Promise(resolve => {
            let els = this.$refs.CanvasBox;
            createImageAndDownload(els[0], img, len > 5, resolve2 => {
              this.curIndex = i + 1;
              this.$nextTick(() => {
                resolve2();
              });
            }).then(() => {
              resolve();
            });
          });
        });
      }

      promiseLine.then(() => {
        if (len > 5) {
          zip.generateAsync({
            type: "blob"
          }).then(content => {
            this.building = false; // 保存到本地

            saveAs(content, "pitcure.zip");
          });
        } else {
          this.building = false;
        }
      });
    },

    importData(e) {
      const file = e.target.files[0];
      const reader = new FileReader();

      reader.onload = event => {
        const fileData = event.target.result; // const data = new Uint8Array(event.target.result);

        const workbook = XLSX.read(fileData, {
          type: 'binary'
        }); // 数据源
        //暂默认读取第一个Sheet

        let sheel0 = workbook.SheetNames[0];
        let blockArr = XLSX.utils.sheet_to_json(workbook.Sheets[sheel0]);
        bus.dataArr = blockArr;
        console.log(' bus.dataArr:', bus.dataArr);
      };

      reader.readAsBinaryString(file);
    }

  }
};