export default {
  props: {
    value: {
      type: String,
      default: ""
    },
    pattern: {
      type: String,
      default: ""
    }
  },

  data() {
    return {};
  },

  computed: {
    formatterData() {
      return Array.from({
        length: 40
      }, () => this.value);
    },

    test() {
      return this.pattern === 'xm' ? 4 : 5;
    }

  }
};